* {
    box-sizing: border-box;
}
.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
}

.dialogsItems {
    border-right: 1px solid black;
    margin-right: 30px;
    padding: 10px;
}
.dialog {
    padding: 5px;
}
.dialog a {
    color: white;
    text-decoration: none;
}

.dialog.active a{
    text-decoration: none;
    color: gold;
}

.messages {
    padding: 10px;
    display: flex;
    flex-direction: column;
}

.messagesElements {
    display: flex;
    margin-top: 25px;
    align-items: center;
}


.messageInput {
    display: inline-block;
    flex: 1 1 70%;
    height: 40px;
}

.messageInput textarea{
    max-width: 600px;
    width: 100%;
    padding: 5px;
    font-size: 14px;
    font-family: "Times New Roman";
    height: 40px;

    resize: none;

}

.sendMessageButton {
    display: inline-block;
    flex: 1 0 20%;
    height: 40px;
}
