.message {
    padding: 5px;
    border: 1px solid gray;
    border-radius: 5px;
    display: inline-block;
    margin-bottom: 5px;
}

.avatarMessage{
    height: 20px;
    width: 20px;
    border-radius: 10px;
    margin-right: 5px;
}
.sentMessage {
    align-self: flex-end;
}

.receivedMessage {
    align-self: flex-start;
    flex: 1 1 50%;
}